<template>
  <div class="container">
    <h1>Instructor Application</h1>
    <div class="course-title">
      <h3>Course</h3>
      <button class="btn btn-success" @click="backCourses()">Back</button>
    </div>
    <div v-if="successMessage" class="alert alert-success alert-dismissable">
      <button class="close" data-dismiss="alert" @click="successMessage = null">
        &times;
      </button>
      {{ successMessage }}
    </div>
    <div v-if="errorMessage" class="alert alert-danger alert-dismissable">
      <button class="close" data-dismiss="alert" @click="errorMessage = null">
        &times;
      </button>
      {{ errorMessage }}
    </div>
    <form>
      <fieldset class="form-group">
        <label>Id</label>
        <input type="text" class="form-control" v-model="course.id" disabled />
      </fieldset>
      <fieldset class="form-group">
        <label>Description</label>
        <input type="text" class="form-control" v-model="course.description" />
      </fieldset>
      <fieldset class="form-group">
        <label>Language</label>
        <input type="text" class="form-control" v-model="course.language" />
      </fieldset>
      <div class="form-group">
        <label for="type">Format</label>
        <div class="form-check">
          <input
            class="form-check-input"
            type="radio"
            v-model="course.format"
            name="format"
            id="in-person"
            value="in-person"
            checked
          />
          <label class="form-check-label" for="inPerson">In-person</label>
        </div>
        <div class="form-check">
          <input
            class="form-check-input"
            type="radio"
            v-model="course.format"
            name="format"
            id="online"
            value="online"
          />
          <label class="form-check-label" for="online">Online</label>
        </div>
      </div>
      <fieldset class="form-group">
        <label>Price</label>
        <input type="text" class="form-control" v-model="course.price" />
      </fieldset>
    </form>
    <button class="btn btn-success" @click="validateAndSubmit()">Save</button>
    <button class="btn btn-warning" @click="backCourses()">Cancel</button>
  </div>
</template>

<script>
import CourseDataService from "../service/CourseDataService";
export default {
  name: "UpdateCourse",
  data() {
    return {
      course: {},
      successMessage: "",
      errorMessage: "",
      coursesList: this.courses,
    };
  },
  props: {
    courses: Array,
  },
  methods: {
    async getCourseDetails(id) {
      try {
        const res = await CourseDataService.retrieveCourse(id);
        this.course = res.data;
      } catch (error) {
        this.errorMessage = "Couldn't get data, try it again later";
      }
    },
    validateAndSubmit() {
      try {
        const newCourse = CourseDataService.validateCourse(this.course);
        this.updateCourse(newCourse);
      } catch (error) {
        this.errorMessage = error.message;
      }
    },
    async updateCourse(newCourse) {
      try {
        const response = await CourseDataService.updateCourse(
          this.course.id,
          newCourse
        );
        this.successMessage = "Course updated successfully";
        this.refreshCoursesList(response.data);
        this.closeMessage();
      } catch (error) {
        this.errorMessage = "Couldn't update this course, try it again later";
      }
    },
    refreshCoursesList(newCourse) {
      const newList = [...this.coursesList];
      const result = newList.filter(
        (item) => item.id !== parseInt(newCourse.id)
      );
      this.coursesList = [...result, newCourse];
      this.coursesList.sort(function (a, b) {
        return a.id - b.id;
      });
      this.$emit("courses", this.coursesList);
      localStorage.setItem("courses", JSON.stringify(this.coursesList));
    },
    closeMessage() {
      setTimeout(() => {
        this.successMessage = null;
      }, 2000);
    },
    backCourses() {
      this.$router.push({ name: "Home" });
    },
  },
  created() {
    this.getCourseDetails(this.$route.params.id);
  },
};
</script>

<style scoped>
.course-title {
  display: flex;
}

.course-title > button {
  margin-left: auto;
  margin-bottom: 10px;
  margin-right: 0;
}

button {
  margin-right: 10px;
}
</style>